@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700,400italic,700italic&subset=latin);

// Customize the background color
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

//customize font
.ui {
  // font-family: sans-serif !important;
  font-family: "Open Sans", "Helvetica Neue", Arial, Helvetica, sans-serif !important;
}

// Customize the menubar color
.ui.menu {
  // background: $background-start;
}

// Customize the home page icon colors
.icon-color {
  // color: silver;
}

@media only screen and (min-width: 1200px) {
  .ui.container.text {
    max-width: 1127px !important;
    margin-left: auto !important;
    margin-right: auto !important;
  }
}

.paragraph {
  font-size: 1.35rem;
  line-height: 1.5;
  color: rgba(0, 0, 0, 0.7);
  margin-top: 10px;
}

.project-image {
  border: 1px solid white;
  border-radius: 4px;
  padding: 5px;
  box-shadow: 0 0 2px 1px rgba(0, 140, 186, 0.5);
  max-width: 250px !important;
}

.ui.grid > .row > [class*="middle aligned"].column,
.ui.grid > [class*="middle aligned"].column:not(.row),
.ui.grid > [class*="middle aligned"].row > .column,
.ui[class*="middle aligned"].grid > .column:not(.row),
.ui[class*="middle aligned"].grid > .row > .column.flex-top {
  align-self: flex-start !important;
}
